'use strict';

import { addItemsToCart } from '../services/cart';
import { createLoadedImage } from '../utils/dom';
import { debounce } from '../utils/performance';
import { formatMoney, generateRandomKey } from '../utils/string';
import { setQueryParams } from '../utils/url';

const REMOVAL_TRANSITION_DURATION = 0.65;

const state = {
  elements: {},
  selectedOptions: {},
  selectedBundleProducts: [],
  selectedQuantity: window.Bundle?.bundleQuantityAllowed || 3,
  selectedVariant: null,
  selectedBundlesTotalQty: 0,
  bundleImagesByVariantId: {},
  renderedImages: [],
  preloadedImagesByVariantId: {},
  totalPrice: {
    price: 0,
    comparePrice: 0,
  }
};

const cacheState = async () => {
  Object.assign(state, window.Bundle);
  const productsContainer = document.querySelector('.Bundle__products');
  state.elements = {
    ...state.elements,
    priceContainer: document.querySelector('.Bundle__price-group'),
    comparePrice: document.getElementById("compare-price"),
    price: document.getElementById("price"),
    options: document.querySelectorAll('.Bundle__option-item'),
    productsContainer,
    errorContainer: document.querySelector('.Bundle__error-container'),
    addToCartBtn: document.querySelector('.Bundle__btn-cart'),
    maxQuantity: document.querySelector('.Bundle__quantity-max'),
    currentSelectedQuantityEl: document.querySelector(
      '.Bundle__quantity-selected'
    ),
    bundledProducts: Array.from(
      productsContainer.querySelectorAll('.Bundle__product')
    ),
    customImageContainer: document.querySelector('.Bundle__images-list'),
    socksBoxImage: document.getElementById('box-image'),
    loader: document.querySelector('.Bundle__loader'),
  };

  state.selectedVariant.options.forEach((option, index) => {
    state.selectedOptions[`option${+index + 1}`] = option;
  });
};

const addEventListeners = () => {
  state.elements.options.forEach((option) => {
    option.addEventListener('click', onOptionSelect);
  });

  state.elements.bundledProducts.forEach((product) => {
    const inputEl = product.querySelector('input');
    const productContent = product.querySelector('.Bundle__product-content');
    inputEl.addEventListener('blur', () => onInputQty(inputEl, product.id));
    product.querySelectorAll('button').forEach((btn) => {
      btn.addEventListener('click', () =>
        onIncrementOrDecrementQty(inputEl, btn, product)
      );
    });
    productContent.addEventListener('click', () =>
      onSelectBundledProduct(product)
    );
  });

  window.addEventListener('resize', debounce(renderAllBundleImages, 500));
  state.elements.addToCartBtn.addEventListener('click', onAddToCart);
};

const getLoadedImagesByQuantity = async () => {
  const imagesLoadPromises = state.selectedBundleProducts.map(
    async ({ id, quantity }, index) => {
      const image = await getLoadedImage(id);
      image.id = `${id}-${index}`;
      if (quantity > 1) {
        const duplicatedImages = [image];
        for (let index = 0; index < quantity - 1; index++) {
          image.id = `${id}-${index}-${quantity}`;
          duplicatedImages.push(image.cloneNode(true));
        }
        return duplicatedImages;
      }
      return image;
    }
  );
  return (await Promise.all(imagesLoadPromises)).flat();
};

const preloadBundleImages = async (variantIds=[]) => {
  const loadImagePromises = variantIds.map(async (variantId) => {
    if (state.preloadedImagesByVariantId.hasOwnProperty(variantId)) {
      return;
    }
    const image = await createLoadedImage(
      state.bundleImagesByVariantId[variantId]
    );
    state.preloadedImagesByVariantId[variantId] = image;
  });
  await Promise.all(loadImagePromises);
};

const renderAllBundleImages = async () => {
  state.elements.customImageContainer.innerHTML = '';
  state.renderedImages = [];
  const preloadedImages = await getLoadedImagesByQuantity();
  preloadedImages.forEach(renderImageIntoBox);
};

const getLoadedImage = async (variantId) => {
  await preloadBundleImages([variantId])
  const image = state.preloadedImagesByVariantId[variantId];
  if (!image) {
    return await new Promise((resolve) => {
      setTimeout(async () => resolve(await getLoadedImage(variantId)), 750);
    });
  }
  return image.cloneNode(true);
};

const renderSingleBundleImage = async (variantId) => {
  const index = state.selectedBundlesTotalQty - 1;
  const image = await getLoadedImage(variantId);
  image.id = `${variantId}-${state.selectedBundlesTotalQty}`;
  renderImageIntoBox(image, index);
};

const removeSingleImageFromBox = async (variantId) => {
  const imageIndex = state.renderedImages.findLastIndex(({ id }) =>
    id.startsWith(variantId)
  );
  if (imageIndex === -1) return;
  const image = state.renderedImages[imageIndex];
  image.style.setProperty('--end-y-transform', `-450%`);
  image.style.opacity = 0.5;
  image.ontransitionend = (e) => {
    if (
      e.propertyName === 'opacity' &&
      e.elapsedTime >= REMOVAL_TRANSITION_DURATION
    ) {
      image.remove();
    }
  };
  state.renderedImages.splice(imageIndex, 1);
  if (imageIndex !== state.renderedImages.length) {
    repositionCurrentImages();
  }
};

const removeAllImagesWithQuantity = (variantId) => {
  [...state.renderedImages].forEach((image) => {
    if (image.id?.startsWith(variantId)) {
      removeSingleImageFromBox(image.id);
    }
  });
};

const repositionCurrentImages = () => {
  state.renderedImages.forEach((image, index) => {
    const imageOffsetTop = getSingleImageOffsetTop(index + 1);
    image.style.setProperty('--end-y-transform', `-${imageOffsetTop}%`);
  });
};

const getSingleImageOffsetTop = (imageIndex) => {
  const boxBounds = state.elements.socksBoxImage.getBoundingClientRect();
  const imageOffsetTop = imageIndex * 95;
  return imageOffsetTop;
};

const renderImageIntoBox = async (image, imageIndex) => {
  imageIndex = isFinite(imageIndex)
    ? imageIndex + 1
    : state.selectedBundlesTotalQty;

  image.className = 'Bundle__child-image';
  state.elements.customImageContainer.insertAdjacentElement(
    'afterbegin',
    image
  );
  state.renderedImages.push(image);

  const imageOffsetTop = getSingleImageOffsetTop(imageIndex);
  image.style.setProperty('--end-y-transform', `-${imageOffsetTop}%`);
};

const fetchRestProducts = async () => {
  try {
    const response = await fetch(`?section_id=${state.elements.productsContainer.dataset.sectionId}&page=2`);
    const htmlParse = new DOMParser();
    const parsedHTML = htmlParse.parseFromString(
      await response.text(), 
      'text/html'
    );
    const productsContainer = parsedHTML.querySelector('.Bundle__products');
    if (productsContainer) {
      const productEls = Array.from(
        productsContainer.querySelectorAll('.Bundle__product')
      ).slice(0, 60 - state.elements.bundledProducts.length);
      productEls.forEach(el => {
        state.elements.productsContainer.insertAdjacentElement('beforeend', el);
        state.elements.bundledProducts.push(el);
      });
    }
  } catch (er) {
    console.log('Error fetching rest products', er);
  }
};

const createBundleImagesMap = () => {
  state.elements.bundledProducts.forEach((productEl) => {
    const { bundleImage } = productEl.dataset;
    const [image, variantId] = bundleImage.split(':');
    state.bundleImagesByVariantId[variantId] = image;
  });
};

const getSelectedBundleById = (variantId) => {
  return state.selectedBundleProducts.find(({ id }) => id === variantId);
};

const onInputQty = (inputEl, variantId) => {
  const prevValue = getSelectedBundleById(variantId).quantity;
  let changeValue = +inputEl.value || prevValue;

  if (prevValue === changeValue || !changeValue) return;

  const difference = changeValue - prevValue;

  if (state.selectedBundlesTotalQty + difference <= state.selectedQuantity) {
    state.selectedBundlesTotalQty += difference;
  } else {
    const difference = state.selectedQuantity - state.selectedBundlesTotalQty;
    changeValue = prevValue + difference;
    state.selectedBundlesTotalQty += difference;
    inputEl.value = prevValue + difference;
  }
  changeSelectedBundleQty(variantId, changeValue);
  toggleDisabledStateIfMaxedOut();
  toggleDisabledBtnState();
  updateSelectedProductsCount();
  renderAllBundleImages();
};

const changeSelectedBundleQty = (variantId, quantity) => {
  state.selectedBundleProducts = state.selectedBundleProducts
    .map((product) => {
      if (!quantity) return null;
      if (product.id === variantId) {
        product.quantity = quantity;
      }
      return product;
    })
    .filter(Boolean);
};

const onIncrementOrDecrementQty = (inputEl, btn, productWrapper) => {
  const variantId = productWrapper.id;
  const price = productWrapper.dataset.price;
  let currentQty = +inputEl.value;
  const isIncrementing = 'increment' in btn.dataset;
  const changeValue = isIncrementing ? 1 : currentQty > 1 ? -1 : 0;
  const prevValue = getSelectedBundleById(variantId).quantity;

  if (state.selectedBundlesTotalQty + changeValue <= state.selectedQuantity) {
    currentQty += changeValue;
    state.selectedBundlesTotalQty += changeValue;
  }
  inputEl.value = Math.max(1, currentQty);

  if (inputEl.value == prevValue) return;

  changeSelectedBundleQty(variantId, currentQty);
  toggleDisabledStateIfMaxedOut();
  toggleDisabledBtnState();
  updateSelectedProductsCount();
  if (isIncrementing) {
    renderSingleBundleImage(variantId);
    state.totalPrice.comparePrice += +price;
  } else {
    removeSingleImageFromBox(variantId);
    state.totalPrice.comparePrice -= +price;
  }
  updatePrice();
};

const toggleOptionActiveState = (selectedOptionValue, selectedOptionIndex) => {
  state.elements.options.forEach((option) => {
    if (selectedOptionIndex !== option.dataset.optionIndex) return;
    option.classList[
      option.dataset.value === selectedOptionValue ? 'add' : 'remove'
    ]('active');
  });
};

const toggleDisabledStateIfMaxedOut = (isAdding) => {
  let isMaxedOut;
  if (typeof isAdding !== 'undefined') {
    isMaxedOut =
      state.selectedQuantity - 1 === state.selectedBundlesTotalQty && isAdding;
  } else {
    isMaxedOut = state.selectedQuantity === state.selectedBundlesTotalQty;
  }
  state.elements.productsContainer.classList[isMaxedOut ? 'add' : 'remove'](
    'Bundle__products--maxed'
  );
};

const toggleDisabledBtnState = () => {
  const { addToCartBtn } = state.elements;
  if (state.selectedBundlesTotalQty < state.selectedQuantity) {
    addToCartBtn.setAttribute('disabled', 'true');
    return;
  }
  addToCartBtn.removeAttribute('disabled');
};

const onOptionSelect = (e) => {
  const selectedOption = e.target;
  const { optionIndex, value } = selectedOption.dataset;

  if (!optionIndex || !value) return;

  const newSelectedOptions = {
    ...state.selectedOptions,
    [`option${optionIndex}`]: value,
  };
  const selectedVariant = updateSelectedVariant(newSelectedOptions);

  if (!selectedVariant) return;

  updateBundledQuantity();
  removeExcessProducts();
  toggleDisabledStateIfMaxedOut();
  toggleDisabledBtnState();
  setQueryParams({ variant: selectedVariant?.id });
  toggleOptionActiveState(value, optionIndex);
};

const toggleProductDisabledState = () => {
  state.elements.bundledProducts.forEach((productEl) => {
    productEl.classList.remove('active');
  });

  state.selectedBundleProducts.forEach(({ id, quantity }) => {
    const productEl = state.elements.bundledProducts.find((el) => el.id === id);
    if (productEl) {
      productEl.classList.add('active');
      const inputEl = productEl.querySelector('input');
      if (inputEl) {
        inputEl.value = quantity;
      }
    }
  });
};

const removeExcessProducts = (maxPairQuantity = state.selectedQuantity) => {
  const productsExceeded = state.selectedBundlesTotalQty > maxPairQuantity;

  if (!productsExceeded) return;

  const { selectedBundleProducts } = state;

  if (selectedBundleProducts.length > state.selectedQuantity) {
    state.selectedBundleProducts = selectedBundleProducts.slice(
      0,
      maxPairQuantity
    );
  }

  let prevQuantity = 0;
  state.selectedBundleProducts = state.selectedBundleProducts
    .map((product) => {
      if (product.quantity + prevQuantity > state.selectedQuantity) {
        product.quantity = state.selectedQuantity - prevQuantity;
      }
      prevQuantity += product.quantity;
      if (!product.quantity) return null;
      return product;
    })
    .filter(Boolean);

  state.selectedBundlesTotalQty = prevQuantity;

  toggleProductDisabledState();
  updateSelectedProductsCount();
  renderAllBundleImages();
};

const updateSelectedVariant = (selectedOptions) => {
  const selectedVariant = state.product.variants.find((variant) =>
    Object.entries(selectedOptions).every(
      ([key, value]) => variant[key] === value
    )
  );

  if (selectedVariant) {
    state.selectedVariant = selectedVariant;
    state.selectedOptions = selectedOptions;
  }

  return selectedVariant;
};

const setAddToCartLabel = (label, fallback = true) => {
  const { addToCartBtn } = state.elements;
  addToCartBtn.textContent = label;
  if (fallback) {
    setTimeout(() => {
      addToCartBtn.textContent = 'ADD TO BAG';
    }, 2500);
  }
};

const setErrorMsg = (message = 'Could not add to cart, please try again') => {
  const { errorContainer } = state.elements;
  errorContainer.textContent = message;
  setTimeout(() => {
    errorContainer.innerHTML = '';
  }, 3000);
};

const onAddToCart = async () => {
  try {
    if (
      !state.selectedVariant ||
      state.selectedBundlesTotalQty < state.selectedQuantity
    ) {
      setErrorMsg('Please select product options');
      return;
    }

    const uniqueBundleKey = generateRandomKey(5);
    const bundleChildItems = state.selectedBundleProducts.map((item) => ({
      ...item,
      properties: {
        __lc_bundles: window.Bundle.bundleId,
        __lc_bundles_parent_variant_id: window.Bundle.parentVariantId,
        __lc_bundles_key: uniqueBundleKey,
        __lc_bundles_bundle_items_count: window.Bundle.bundleQuantityAllowed,
        __lc_bundles_discount_id: window.Bundle.bundleId,
        __lc_bundles_is_native: true,
      },
    }));
    if (!bundleChildItems.length) {
      setAddToCartLabel("Please select products to add");
      return;
    }

    setAddToCartLabel('ADDING...', false);
    const response = await addItemsToCart(bundleChildItems);
    const isError = response?.status?.toString()?.startsWith("4");
    setAddToCartLabel(isError ? "SOMETHING WENT WRONG!" : "ADDED!");
    if (!isError) {
      resetSelections();
    }
    if (isError) {
      setErrorMsg(response.message);
    }
  } catch (er) {
    console.log(er);
    setAddToCartLabel('SOMETHING WENT WRONG!');
  }
};

const updateBundledQuantity = () => {
  const selectedPairQuantity = Number(
    state.selectedVariant.option1?.match(/^\d+|\d+\b|\d+(?=\w)/g)?.[0]
  );
  if (!selectedPairQuantity) return;
  state.selectedQuantity = selectedPairQuantity;
  state.elements.maxQuantity.textContent = selectedPairQuantity;
};

const updateSelectedProductsCount = () => {
  state.elements.currentSelectedQuantityEl.textContent =
    state.selectedBundlesTotalQty;
};

const updatePrice = () => {
  const { value, type } = window.Bundle.discount;
  if (type === "percentage") {
    state.totalPrice.price = state.totalPrice.comparePrice / 100 * (100 - value);
  } else {
    state.totalPrice.price = state.totalPrice.comparePrice;
  }

  if (state.totalPrice.comparePrice > state.totalPrice.price) {
    state.elements.comparePrice.textContent = formatMoney(state.totalPrice.comparePrice)
    state.elements.price.textContent = formatMoney(state.totalPrice.price)
  } else {
    state.elements.price.textContent = formatMoney(state.totalPrice.comparePrice)
  }
  state.elements.priceContainer.classList.toggle("empty-price", !state.totalPrice.comparePrice > 0)
}

const onSelectBundledProduct = (productWrapper) => {
  const id = productWrapper.id;
  const isSelected = state.selectedBundleProducts.some(
    (product) => product.id === id
  );
  const isMaxQuantitySelected =
    state.selectedQuantity === state.selectedBundlesTotalQty;

  toggleDisabledStateIfMaxedOut(!isSelected);

  const inputEl = productWrapper.querySelector('input');
  const selectedQuantity = +inputEl.value;

  if (!isSelected && !isMaxQuantitySelected) {
    state.selectedBundleProducts.push({
      id,
      quantity: selectedQuantity,
      productWrapper,
    });
    state.selectedBundlesTotalQty += selectedQuantity;
    productWrapper.classList.add('active');
    renderSingleBundleImage(id);
    state.totalPrice.comparePrice += +productWrapper.dataset.price;
  } else if (isSelected) {
    state.selectedBundleProducts = state.selectedBundleProducts.filter(
      ({ id: variantId }) => variantId !== id
    );
    inputEl.value = 1;
    state.selectedBundlesTotalQty -= selectedQuantity;
    productWrapper.classList.remove('active');
    removeAllImagesWithQuantity(id);
    state.totalPrice.comparePrice -= +productWrapper.dataset.price;
  }

  toggleDisabledBtnState();
  updateSelectedProductsCount();
  updatePrice();
};

const resetPrice = () => {
  state.elements.price.textContent = "";
  state.elements.comparePrice.textContent = "";

  state.totalPrice.price = 0;
  state.totalPrice.comparePrice = 0;

  state.elements.priceContainer.classList.add("empty-price")
}

const resetSelections = () => {
  state.selectedBundleProducts.forEach(selection => {
    const id = selection.productWrapper.id;
    const inputEl = selection.productWrapper.querySelector('input');

    inputEl.value = 1;
    selection.productWrapper.classList.remove('active');
    removeAllImagesWithQuantity(id);
  })
  state.selectedBundlesTotalQty = 0;
  state.selectedBundleProducts = [];
  toggleDisabledStateIfMaxedOut();
  toggleDisabledBtnState();
  updateSelectedProductsCount();
  resetPrice();
}

const init = async () => {
  if (!window.Bundle?.product) return;
  await cacheState();
  await fetchRestProducts();
  createBundleImagesMap();
  addEventListeners();
};

export default init;
